import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { TasLoginComponent } from './tas-login/tas-login.component';

const routes: Routes = [{
  path:'login',component:TasLoginComponent
}];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class TasLoginRoutingModule { }
