import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { HealthCareService } from '../../../services/healthcare-service';
import { PatientData } from './../../../models/patient-data';

@Component({
  selector: 'app-tas-login',
  templateUrl: './tas-login.component.html',
  styleUrls: ['./tas-login.component.scss']
})
export class TasLoginComponent implements OnInit {

  user = new PatientData();
  constructor(private router: Router,private healthcareService: HealthCareService) { }

  ngOnInit() {
    console.log(this.user);
    
  }
  Click() {
    this.router.navigate(['/patient-info'])
  }

  patientLogin() {
    if(this.user.email && this.user.password){
      this.healthcareService.patientLogin(this.user).subscribe((user) => {
        if (user) {
          localStorage.setItem('user_data', JSON.stringify(user));
          alert('Signed in Successfully');
          this.router.navigate(['/patient-info'])
        }
      }, (err: any) => {
        //alert(err);
      });
    }
  }

}
