import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { HealthCareService } from 'src/services/healthcare-service';

@Component({
  selector: 'app-display-info',
  templateUrl: './display-info.component.html',
  styleUrls: ['./display-info.component.scss']
})
export class DisplayInfoComponent implements OnInit {

  constructor(private router:Router,private healthcareService: HealthCareService) { }

  ngOnInit() {
    //debugger
    if(!localStorage.getItem('user_data')){
      this.router.navigate(['/login'])
    }
  }

}
