import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpErrorResponse } from '@angular/common/http';
import { Observable, of, throwError } from 'rxjs';
import { map, catchError, tap } from 'rxjs/operators';
import { PatientData } from '../.../../models/patient-data';
import { environment } from '../.../../environments/environment';
import { Router } from '@angular/router';
@Injectable({
    providedIn: "root"
})
export class HealthCareService {
    url = environment.url;
    constructor(private router: Router,private http: HttpClient) { }
    patientLogin(patient: PatientData): Observable<PatientData> {
        return this.http
            .post<any>(this.url + "patientLogin", patient)
            .pipe(catchError(this.handleErrorObservable));
    }


    getPatientHealthInfo(): Observable<any> {
        return this.http
            .get<any>(this.url + "getPatientHealthInfo")
            .pipe(catchError(this.handleErrorObservable));
    }

    get user() {
        const user = JSON.parse(localStorage.getItem('user_data'))
        if (user) {
            return user["Personal info"].Name;
        } else {
          
            return false;
        }

    }
    get userPersonalInfo() {
        const user = JSON.parse(localStorage.getItem('user_data'))
        if (user) {
            return user["Personal info"];
        } else {
          
            return false;
        }

    }

    get userMedicalCondition() {
        const user = JSON.parse(localStorage.getItem('user_data'))
        if (user) {
            return user["Medical Condition"];
        } else {
          
            return false;
        }

    }


    get userHealthStatus() {
        const user = JSON.parse(localStorage.getItem('user_data'))
        if (user) {
            return user["Health Status"];
        } else {
          
            return false;
        }

    }
    private handleErrorObservable(error: Response | any) {
        console.error(error.message || error);
        return throwError(error);
    }
}
